<template>
  <div class="setting-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item><a href="/"></a></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-row>
        <el-col :span="16">
          <el-form
            ref="form"
            :model="user"
            :rules="rules"
            label-width="80px"
          >
            <el-form-item label="">
              {{user.id}}
            </el-form-item>
            <el-form-item label="">
              {{user.mobile}}
            </el-form-item>
            <el-form-item label="" prop="name">
              <el-input v-model="user.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="intro">
              <el-input type="textarea" v-model="user.intro"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="user.email" prop="email"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                @click="onSubmit"
                :loading="isSaveProfile"
              ></el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :offset="3" :span="4">
          <label for="file">
            <el-avatar
              shape="square"
              :size="100"
              fit="cover"
              :src="user.photo">
            </el-avatar>
            <p style="font-size:14px;"></p>
          </label>
          <input
            type="file"
            id="file"
            hidden
            @change="onChange"
            ref="file"
          >
        </el-col>
      </el-row>
    </el-card>

    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="30%"
      append-to-body
      @opened='onDialogopene'
      @close='onDialogclose'
    >
      <div class="preview-img-wrap">
        <img
          class="preview-img"
          :src="previewImage"
          ref="preview-img"
        >
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="onUpdatePhoto"
          :loading="isLoading"
          > </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { userProfile, updateUserPhoto, updateUserProfile } from '@/api/user'
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'

import bus from '@/globle-bus'

export default {
  name: 'Setting',
  components: {},
  props: {},
  data () {
    return {
      user: {},
      dialogVisible: false,
      previewImage: '', // 
      cropper: null, // 
      isLoading: false,
      isSaveProfile: false,
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 7, message: ' 1  7 ', trigger: 'blur' }
        ],
        intro: [
          { required: true, message: '', trigger: 'blur' },
          { min: 10, message: '10', trigger: 'change' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 7, message: ' 1  7 ', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadUser()
  },
  mounted () {},
  methods: {
    onSubmit () {
      // 
      this.$refs.form.validate(valid => {
        if (!valid) {
          return false
        } else {
          // 
          this.isSaveProfile = true
          const { name, email, intro } = this.user
          updateUserProfile({
            name,
            email,
            intro
          }).then(() => {
            this.isSaveProfile = false
            bus.$emit('article-user', this.user)
            this.$message({
              type: 'success',
              message: ''
            })
          })
        }
      })
    },
    // 
    loadUser () {
      userProfile().then(res => {
        this.user = res.data.data
      })
    },
    onChange () {
      // 
      const file = this.$refs.file
      // API
      const blobData = window.URL.createObjectURL(file.files[0])
      this.previewImage = blobData
      // 
      this.dialogVisible = true
      //  change 
      this.$refs.file.value = ''
    },
    onDialogopene () {
      //  DOM
      const image = this.$refs['preview-img']
      //  img 
      //  img  
      // 
      this.cropper = new Cropper(image, {
        // 
        aspectRatio: 1 / 1,
        // 
        viewMode: 1,
        // move:  none:
        dragMode: 'move',
        // 
        background: false,
        // 
        cropBoxResizable: false
        // dragMode
        // movable: false
        // crop 
        // crop (event) {
        //   console.log(event.detail.x)
        //   console.log(event.detail.y)
        //   console.log(event.detail.width)
        //   console.log(event.detail.height)
        //   console.log(event.detail.rotate)
        //   console.log(event.detail.scaleX)
        //   console.log(event.detail.scaleY)
        // }
      })
    },
    // 
    onDialogclose () {
      this.cropper.destroy()
    },
    // 
    onUpdatePhoto () {
      this.isLoading = true
      // 
      this.cropper.getCroppedCanvas().toBlob(file => {
        const fd = new FormData()
        fd.append('photo', file)
        //  fb
        updateUserPhoto(fd).then(res => {
          console.log(res)
          // 
          this.dialogVisible = false
          // 
          // this.user.photo = res.data.data.photo
          //  blob
          this.user.photo = window.URL.createObjectURL(file)
          this.isLoading = true
          bus.$emit('article-user', this.user)
          // 
          this.$message({
            type: 'success',
            message: ''
          })
        })
      })
    }
  }
}
</script>

<style lang="less" scoped>
.preview-img-wrap{
  .preview-img{
    display: block;
    max-width: 100%;
    height: 200px;
  }
}
</style>
